/**
 * prefix
 *
 * @param  {Array} prefix 
 * @param  {string} split 
 * @return {[RegExp]}  
 */
export const getRegExp = (prefix, split) => {
    // 
    const splitRegExp = split.replace(/[-\/\\^$*+?.()|[\]{}]/g, '\\$&');
    const prefixArray = Array.isArray(prefix) ? prefix : [prefix];
    let prefixToken = prefixArray.join('').replace(/(\$|\^)/g, '\\$1');

    if (prefixArray.length > 1) {
        prefixToken = `[${prefixToken}]`;
    }

    return new RegExp(`(${splitRegExp}|^)(${prefixToken})([^${splitRegExp}]*)$`, 'g');
};

/**
 * 
 * todo  
 *
 * @param  {string} str    
 * @param  {number} start  
 * @param  {number} end    
 * @param  {string} newStr 
 * @param  {string} split  
 * @return {[type]}        
 */
export const insertString = (str, start, end, newStr, split) => (
    str.slice(0, start) + newStr + split + str.slice(end)
);

/**
 * setCursorPosition 
 *
 * @param {string} selector dom
 * @param  {number} pos 
 */
export const setCursorPosition = (selector, pos) => {
    let range;
    if (document.createRange) { // Firefox, Chrome, Opera, Safari, IE 9+
        range = document.createRange(); // 
        range.selectNodeContents(selector); // 
        if (selector.innerText.length > 0) {
            range.setStart(selector.childNodes[0], pos); // 
        }
        range.collapse(true); // 
        let selection = window.getSelection();// 
        selection.removeAllRanges(); // 
        selection.addRange(range); // 
    }
    else if (document.selection) { // IE 8 and lower
        range = document.body.createTextRange();
        range.moveToElementText(selector);
        range.collapse(false);
        range.select();
    }
};

/**
 * getSearchWordPos 
 *
 * @param {string} el dom
 * @return  {Object} 
 */
export const getSearchWordPos = el => {
    let range = window.getSelection().getRangeAt(0).cloneRange();
    let rects = range.getClientRects();
    if (!rects[0]) {
        return {};
    }
    let {left, top} = rects[0];
    let clientRect = el.getBoundingClientRect();
    let computedStyle = getComputedStyle(el);
    return {
        left: left - clientRect.left,
        top: top - clientRect.top,
        height: parseInt(computedStyle.lineHeight, 10)
    };
};
